<template>
	<div class="newbuilt_wrap" data-commont="新建素材">
		<el-row class="back">
			<el-col :span="0.8">
				<router-link :to="{path:'/systemUser_list'}">
					<span class="el-icon-arrow-left back_span">返回</span>
				</router-link>
			</el-col>
			<el-col :span="0.4" style="color: #ccc;">|</el-col>
			<el-col :span="2">
				<span>{{planName}}</span>
			</el-col>
		</el-row>
		<el-row style ="margin-top: 20px;">
			<el-col>
				<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="200px" class="demo-ruleForm">
				    <el-form-item label="用户名" prop="USERNAME"> 
				    	<el-input v-model="ruleForm.USERNAME" placeholder="请输入你的用户名" style = "width: 800px;"></el-input>
				    </el-form-item>
				    <el-form-item label="用户描述" prop="UNAME"> 
				    	<el-input v-model="ruleForm.UNAME" placeholder="请输入你的用户描述" style = "width: 800px;"></el-input>
				    </el-form-item>
				    <el-form-item label="角色" prop="ROLE_ID">
					    <el-select v-model="ruleForm.ROLE_ID" placeholder="请选择你的角色"style = "width: 800px;"   >
					        <el-option v-for = "(item,index) in roleData" :key="item.value" :value="item.value" :label="item.label"></el-option>
					    </el-select>
				    </el-form-item>
				    <el-form-item label="邮箱" prop="EMAIL"> 
				    	<el-input v-model="ruleForm.EMAIL" placeholder="请输入你的职位" style = "width: 800px;"></el-input>
				    </el-form-item>
				    <el-form-item label="职位" prop="UPOSITION"> 
				    	<el-input v-model="ruleForm.UPOSITION" placeholder="请输入你的职位" style = "width: 800px;"></el-input>
				    </el-form-item>
				    <el-form-item label="手机号码" prop="PHONE"> 
				    	<el-input v-model="ruleForm.PHONE" placeholder="请输入你的手机号码" style = "width: 800px;"></el-input>
				    </el-form-item>
				    <el-form-item label="公司" prop="UCOMPANY"> 
				    	<el-input v-model="ruleForm.UCOMPANY" placeholder="输入你所属公司" style = "width: 800px;"></el-input>
				    </el-form-item>
				    <!--<el-form-item label="用户描述" prop="userDesc"> 
				    	<el-input v-model="ruleForm.userDesc" type="textarea" :rows="5" placeholder="最多可输入120字符" style = "width: 800px;"></el-input>
				    </el-form-item>-->
				    
				    
				    <el-form-item label="状态">
						<el-checkbox v-model="ruleForm.STATUS"></el-checkbox>
				    </el-form-item>
				    
				</el-form>
			</el-col>
		</el-row>
		
		<!--完成-->
		<div class="complete" style="text-align: center;">
			<span v-if ="editId">
				<el-button type="primary"style="width: 100px;margin: 0 auto;" @click="updateFun('ruleForm')">修改保存</el-button>
			</span>
			<span v-else>
				<el-button type="primary"style="width: 100px;margin: 0 auto;" @click="complete('ruleForm')">完成</el-button>
			</span>
			
		</div>
	</div>
</template>

<script>
	
	import validator from 'utils/validator'  //正则
	import {
		saveUsersPort,   //新增保存
		getUsersPort ,  //编辑初始化
		updateUserPost    //更新保存
	}from 'api/systemUser'
	
	export default{
		name:'addEidtUser',
		data(){
			return {
				planName:'新建用户',
				editId:this.$route.params.id,  ///编辑id
				ruleForm: {
					USERNAME:'',
		            UNAME: '',
		   			ROLE_ID:'',
		   			UPOSITION:'',
		   			PHONE:'',
		   			EMAIL:'',
		   			UCOMPANY:'',
		   			STATUS:0
		       },
		        rules: {
		        	USERNAME: [
			            { required: true, message: '请输入用户名', trigger: 'blur' },
			            { min: 3, max: 8, message: '长度在3-8个字符之间', trigger: 'blur' }
			        ],
			        UNAME: [
			            { required: true, message: '请输入你的姓名', trigger: 'blur' },
			            { min: 2, max: 8, message: '长度在2-8个字符之间', trigger: 'blur' }
			        ],
			        UPOSITION: [
			            { required: true, message: '请输入你的职位', trigger: 'blur' },
			            { min: 2, max: 8, message: '长度在2-8个字符之间', trigger: 'blur' }
			        ], 
			        UCOMPANY: [
			            { required: true, message: '请输入所属公司', trigger: 'blur' },
			            { min: 2, max: 20, message: '长度在2-20个字符之间', trigger: 'blur' }
			        ],
//			        userDesc: [
//			            { required: true, message: '请输入你的用户描述', trigger: 'blur' },
//			            { min: 3, max:120, message: '长度在3-120个字符之间', trigger: 'blur' }
//			        ],
			        ROLE_ID: [
			            { required: true, message: '请选择课程分类', trigger: 'change' }
			        ],
			        PHONE: [{validator:validator.isNumberValid, trigger: 'blur' } ],
			        EMAIL:[{validator:validator.isEailValid, trigger: 'blur' } ],

				},
				
				
				roleData:[{
					value:1,
					label:'普通管理员'
				},{
					value:2,
					label:'高级管理员'
				}],
		    }
		},
		created(){
			if(this.editId){
				this.planName = "编辑人员"
				this.editInit()
			}
		},
		methods:{
			//编辑初始化
			editInit(){
				let resData = {
					USER_ID:this.editId,
					limit:1,
					offset:5
				}
				getUsersPort(resData).then( res =>{
					for(var item of res.ALLUSER){
						this.ruleForm= item
						console.log(JSON.stringify(item))
					}
//	  				console.log('初始化编辑'+JSON.stringify(res))
	  			})
			},
			//编辑保存
			updateFun(){
				let resData = {
					USER_ID:this.editId,
					USERNAME:this.ruleForm.USERNAME,    //用户名
            		UNAME:this.ruleForm.UNAME,      //用户描述
            		ROLE_ID:this.ruleForm.ROLE_ID,     //角色
            		STATUS:this.ruleForm.STATUS==true ? 1:0,	//状态
            		EMAIL:this.ruleForm.STATUS,		//邮箱
            		PHONE:this.ruleForm.PHONE,	//手机号，
            		UCOMPANY:this.ruleForm.UCOMPANY ,//公司
            		UPOSITION:this.ruleForm.UPOSITION,	//职位
				}
				updateUserPost(resData).then( res =>{
					console.log('编辑保存'+JSON.stringify(res))
					this.$router.push({
						name:'systemUser_list'
					})
				})
			},
		    //完成
		    complete(formName){
		    	this.$refs[formName].validate((valid) => {
		            if (valid) {
		            	var resData = {
		            		USERNAME:this.ruleForm.USERNAME,    //用户名
		            		UNAME:this.ruleForm.UNAME,      //用户描述
		            		ROLE_ID:this.ruleForm.ROLE_ID,     //角色
		            		STATUS:this.ruleForm.STATUS==true ? 1:0,	//状态
		            		EMAIL:this.ruleForm.STATUS,		//邮箱
		            		PHONE:this.ruleForm.PHONE,	//手机号，
		            		UGROUP:'',
		            		UPLATFORM:' ',	//系统平台
		            		UCOMPANY:this.ruleForm.UCOMPANY ,//公司
		            		UCOSTCENTER:'',
		            		UPOSITION:this.ruleForm.UPOSITION,	//职位
		            		UORGANIZATION:'',  //组织单元
		            		INTERSTART:'', //有效期开始
		            		INTERDURA:'', 	//有效期间
		            		OPENID:'',	//微信公众号id

		            	}
//		            	console.log('resData:::'+JSON.stringify(resData))
		            	saveUsersPort(resData).then( response =>{
//		            		console.log('保存'+JSON.stringify(response))
		            		this.$router.push({
		            			name:'systemUser_list'
		            		})
		            	})
		           		
		            } else {
		            	console.log('error submit!!');
		            return false;
		            }
		        });
		  	},
		  	handleClose(done) {
		        this.$confirm('确认关闭？')
		          	.then(()=> {
		            	done();
		          	})
		          	.catch(()=> {});
		    }
		}
	}	

</script>

<style lang="scss" slot-scope="scope">
	$border:1px solid #eee;
	.newbuilt_wrap{
		background: #fff;
		padding-bottom: 20px;
		.back{	
			height: 50px;
			line-height: 50px;
			border-bottom: $border;
			font-size:1.4rem;
			div{
				margin-left: 10px;
				
			}
		}
		
	}
	
</style>


