import fetch from 'utils/fetch';
// 引入公共常量
import publicConst from 'utils/public_const'

/**
 * 查询用户信息
 */

export function getUsersPort(query) {
  	return fetch({
	    url: publicConst.proxyPath+'queryUser',
	    method: 'get',
	    params:query,
  	});
}

/**
 * 新增用户信息
 */

export function saveUsersPort(query) {
  	return fetch({
	    url: publicConst.proxyPath+'saveUser',
	    method: 'get',
	    params:query,
  	});
}

/*
* 删除用户信息
*/
export function deletePLUserPost(query) {
	return fetch({
	    url: publicConst.proxyPath+'deletePLUserByID',
	    method: 'get',
	    params:query,
	});
}

/*
* 更新用户信息
*/
export function updateUserPost(query) {
	return fetch({
	    url: publicConst.proxyPath+'updateUser',
	    method: 'get',
	    params:query,
	});
}




